import { UserState } from "../types";
import { ADD_USER, UserActions } from "./userActions";
const initialState: UserState = {
  user: [],
};

export default (state = initialState, action: UserActions) => {
  switch (action.type) {
    case ADD_USER:
      return { ...state, ...action.payload };

    default:
      return state;
  }
};
